'use strict';

import Alpine from 'alpinejs';
import screenfull from 'screenfull';
import EventTracker from './EventTracker';

export default class ModelViewer {
    constructor() {
        const modelViewer = this;

        [this.enrichment, this.assetBundle, this.labels] = extractEnrichmentDocumentData();

        if (!this.assetBundle && !this.labels) {
            [this.assetBundle, this.labels] = extractAssetBundleDocumentData();
        }

        if (!this.assetBundle && !this.labels) {
            console.error('No asset bundle or labels found in the document');
        }

        document.addEventListener('alpine:init', () => {
            Alpine.data('ModelViewer', () => ({
                widescreen: false,
                supportsFullscreen: false,
                isFullscreen: false,
                eventTracker: null,
                isLabelExpanded: false,
                init() {
                    const self = this;
                    this.eventTracker = new EventTracker();
                    if (modelViewer.enrichment) {
                        this.eventTracker.modelTriggered(modelViewer.enrichment);
                    }
                    const updateWidescreen = () => {
                        this.widescreen = window.innerWidth > 768;
                        this.isLabelExpanded = this.widescreen;
                    };
                    this.supportsFullscreen = screenfull.isEnabled;

                    updateWidescreen();
                    window.addEventListener('resize', updateWidescreen, false);
                    window.addEventListener('hashchange', function(event) {
                        const currentLabel = self.findCurrentLabelElement();
                        if (currentLabel) {
                            if (self.isLabelExpanded) {
                                currentLabel.setAttribute('open', '');
                            } else {
                                currentLabel.removeAttribute('open');
                            }
                        }

                        self.trackLabelEvent(event);
                    }, false);
                },
                async fullscreen() {
                    await screenfull.toggle(document.documentElement);
                    this.isFullscreen = screenfull.isFullscreen;
                },
                findCurrentLabelElement() {
                    const currentLabelID = document.location.hash.split('#')[1];
                    if (!currentLabelID) {
                        return null;
                    }

                    return document.getElementById(currentLabelID);
                },
                findCurrentLabelObject() {
                    const labelUid = document.location.hash.split('#label-')[1];
                    if (!labelUid) {
                        return null
                    }

                    return modelViewer.labels.find(label => label.uid.replaceAll('-', '') === labelUid);
                },
                findPreviousAndNextLabelElements() {
                    const currentLabelID = document.location.hash.split('#')[1];
                    if (!currentLabelID) {
                        return { previous: null, next: null };
                    }

                    const currentLabel = document.getElementById(currentLabelID);
                    if (!currentLabel) {
                        return { previous: null, next: null };
                    }

                    const nextLink = currentLabel.querySelector('a[rel="next"]');
                    const previousLink = currentLabel.querySelector('a[rel="prev"]');
                    const next = nextLink ? document.getElementById(nextLink.href.split('#')[1]) : null;
                    const previous = previousLink ? document.getElementById(previousLink.href.split('#')[1]) : null;

                    return { previous, next };
                },
                previousLabel() {
                    const labels = this.findPreviousAndNextLabelElements();
                    if (!labels.previous) {
                        return;
                    }

                    document.location.hash = labels.previous.id;
                },
                nextLabel() {
                    const labels = this.findPreviousAndNextLabelElements();
                    if (!labels.next) {
                        return;
                    }

                    document.location.hash = labels.next.id;
                },
                labelExpanded(event) {
                    this.isLabelExpanded = event.target?.parentElement?.hasAttribute('open') === false;

                    if (this.isLabelExpanded) {
                        const label = this.findCurrentLabelObject();
                        this.eventTracker.modelLabelExpanded(label);
                    }
                },
                trackLabelEvent(event) {
                    if (event.newURL === event.oldURL) {
                        return;
                    }

                    const label = this.findCurrentLabelElement();
                    if (!label) {
                        return;
                    }

                    this.eventTracker.modelLabelTriggered(label);

                    if (this.isLabelExpanded) {
                        this.eventTracker.modelLabelExpanded(label);
                    }
                }
            }));
        });

        if (!window.Alpine) {
            window.Alpine = Alpine;
            Alpine.start();
        }
    }
}

function extractAssetBundleDocumentData() {
    const assetBundleScript = document.querySelector('script#asset-bundle');
    if (!assetBundleScript) {
        return [null, null];
    }

    const assetBundle = JSON.parse(assetBundleScript.innerHTML);
    if (!assetBundle) {
        console.error('The asset bundle JSON is empty or invalid.');
        return [null, null];
    }

    const labels = assetBundle.labels;

    for (var label of labels) {
        label.assetBundle = assetBundle;
    }

    return [assetBundle, labels];
}

function extractEnrichmentDocumentData() {
    const enrichmentScript = document.querySelector('script#enrichment');
    if (!enrichmentScript) {
        return [null, null];
    }

    const enrichment = JSON.parse(enrichmentScript.innerHTML);
    if (!enrichment) {
        console.error('The enrichment JSON is empty or invalid.');
        return [null, null];
    }

    const labels = enrichment.modelLabels;

    for (var label of labels) {
        label.enrichment = enrichment;
    }

    return [enrichment, enrichment.assetBundle, labels];
}
