import { Cache, Vector3, Clock, Scene, PerspectiveCamera, WebGLRenderer, SRGBColorSpace, PMREMGenerator, AnimationMixer, Vector2, Raycaster } from 'three';
import { OrbitControls } from 'three/addons/controls/OrbitControls';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader';
import { DRACOLoader } from 'three/addons/loaders/DRACOLoader';
import { GainMapLoader } from '@monogrid/gainmap-js';
import { update } from '@tweenjs/tween.js';
import { createLabel } from './CreateLabel';
import { animateCameraMove, resetCamera } from './CameraTransitions';
import * as StyleBuilder from './StyleBuilder';
import ModelViewer from './ModelViewer';

export let scene, camera, renderer, controls, mixer;
export const center = new Vector3(0,0,0);
export const labelPoints = [];
const clock = new Clock();

(async function() {
    StyleBuilder.append();
    const modelViewer = new ModelViewer();

    const container = document.querySelector('#canvas-container');
    if (!container) {
        console.error('No container found');
        return;
    }

    const glbUrl = modelViewer.assetBundle?.buildTargets?.glb;
    if (!glbUrl) {
        console.error('No GLB found in asset bundle', modelViewer.assetBundle);
        return;
    }

    const boundingBox = modelViewer.assetBundle?.boundingBox;
    if (!boundingBox) {
        console.error('No bounding box found in asset bundle', modelViewer.assetBundle);
        return;
    }

    await renderModel(container, glbUrl, modelViewer.labels, boundingBox);
})();

async function renderModel(container, glbUrl, labels, boundingBox) {
    const initialized = !!renderer;
    Cache.enabled = true;
    renderer = await seUpScene(glbUrl);

    container.removeAttribute('aria-busy');
    container.appendChild(renderer.domElement);

    setUpControls((boundingBox.minY + boundingBox.maxY) * 0.5);
    await addLabels(labels);
    window.addEventListener('resize', onWindowResize, false);
    window.addEventListener('hashchange', visualSelectLabel, false);
    setUpClickListener(container);

    if (!initialized) {
        requestAnimationFrame(animate);
    }

    visualSelectLabel();
}

function setUpControls(height) {
    controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true;
    controls.dampingFactor = 0.25;
    center.y = height;
    controls.target.set(center.x,center.y, center.z);
    controls.maxPolarAngle = 3.14 - 0.4;
    controls.minPolarAngle = 0.4;
    controls.maxDistance = 4;
    controls.minDistance = 0.1;
}

async function seUpScene(glbUrl) {
    scene = new Scene();
    camera = new PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.01, 100);
    camera.position.set(-1, 0.7, -1);
    renderer = new WebGLRenderer({ antialias: true, alpha: true, preserveDrawingBuffer: true });
    renderer.setClearColor(0x000000, 0);
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.outputEncoding = SRGBColorSpace;
    scene.environment = await loadHDRTexture(renderer);
    const gltf = await loadGlb(glbUrl);
    scene.add(gltf.scene);
    scene.environmentRotation.y = Math.PI;
    return renderer;
}

async function loadHDRTexture(renderer) {
    const loader = new GainMapLoader(renderer);
    const result = await loader.loadAsync(['/img/environment.webp', '/img/environment.gainmap.webp', '/img/environment.metadata.json']);
    const texture = result.renderTarget.texture;
    const pmremGenerator = new PMREMGenerator(renderer);
    const envMap = pmremGenerator.fromEquirectangular(texture).texture;

    texture.dispose();
    result.dispose();

    return envMap;
}

async function loadGlb(glbUrl) {
    const dracoLoader = new DRACOLoader();
    dracoLoader.setDecoderPath('https://www.gstatic.com/draco/v1/decoders/');

    const loader = new GLTFLoader();
    loader.setDRACOLoader(dracoLoader);

    return new Promise(function(resolve, reject) {
        loader.load(glbUrl, function(gltf) {
            if (gltf.animations) {
                mixer = new AnimationMixer(gltf.scene);
                gltf.animations.forEach((clip) => mixer.clipAction(clip).play());
            }

            resolve(gltf);
        }, undefined, reject);
    });
}

async function addLabels(labels) {
    var labelIndex = 1;
    for (const label of labels) {
        const positionX = -label.anchorPosition["positionX"];
        const positionY = label.anchorPosition["positionY"];
        const positionZ = label.anchorPosition["positionZ"];
        const point = new Vector3(positionX, positionY, positionZ);
        const uid = label.uid.replaceAll('-', '');
        await createLabel(point, labelIndex, label.title, label.description, uid, camera, scene, labelPoints);
        labelIndex++;
    }
}

function selectLabel(labelUid) {
    const prefixedLabelUid = `label-${labelUid.replaceAll('-', '')}`;

    if (window.location.hash !== prefixedLabelUid) {
        window.location.hash = prefixedLabelUid;
    }
}

function visualSelectLabel() {
    if (!window.location.hash.startsWith('#label-')) {
        return;
    }

    visualDeselectAllLabels();

    const selectedLabelUid = window.location.hash.replace('#label-', '').trim().replaceAll('-', '');
    if (selectedLabelUid === '') {
        return;
    }

    const labelPoint = labelPoints.find(labelPoint => {
        const uid = labelPoint.userData.uid.replaceAll('-', '');
        return uid === selectedLabelUid;
    });
    if (!labelPoint) {
        console.error('Label not found', { labelUid: selectedLabelUid, labelPoints });
        return;
    }

    labelPoint.children[0].material.uniforms.selected.value = true;
    labelPoint.children[1].material.uniforms.selected.value = true;

    animateCameraMove(labelPoint.position);
}

function animate() {
    const delta = clock.getDelta();

    if (mixer) {
        mixer.update(delta);
    }

    controls.update();
    labelPoints.forEach(label => label.children[1].material.uniforms.time.value += delta);

    camera.updateMatrixWorld(true);
    update();
    renderer.clear(false, true, false);
    renderer.render(scene, camera);
    requestAnimationFrame(animate);
}

function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
    console
    labelPoints.forEach(label => {
        label.children[0].material.uniforms.screenHeight.value = window.innerHeight;
        label.children[1].material.uniforms.screenHeight.value = window.innerHeight;
    });
}

function visualDeselectAllLabels() {
    labelPoints.forEach(label => {
        label.children[0].material.uniforms.selected.value = false;
        label.children[1].material.uniforms.selected.value = false;
    });

    resetCamera();
}

function setUpClickListener(container) {
    let isDragging = false;
    let startX, startY;

    container.addEventListener('mousedown', (event) => {
        labelPoints.forEach(label => {
            const scale = label.position.clone().distanceTo(camera.position) / (window.innerHeight /105.0);
            label.scale.set(scale, scale, scale);
        });
        isDragging = false;
        startX = event.clientX;
        startY = event.clientY;

        const onMouseMove = (moveEvent) => {
            if (Math.abs(moveEvent.clientX - startX) > 5 || Math.abs(moveEvent.clientY - startY) > 5) {
                isDragging = true;
            }
        };

        const onMouseUp = (upEvent) => {
            if (!isDragging) {
                onDocumentClicked(upEvent);
            }
            container.removeEventListener('mousemove', onMouseMove);
            container.removeEventListener('mouseup', onMouseUp);
        };

        container.addEventListener('mousemove', onMouseMove);
        container.addEventListener('mouseup', onMouseUp);
    });
}

function onDocumentClicked(event) {
    const intersects = RaycastForLabel(event);

    if (intersects.length > 0) {
        const labelUid = intersects[0].object.userData.uid;
        selectLabel(labelUid);
    }
    else
    {
        if (window.location.hash !== '') {
            visualDeselectAllLabels();
            window.location.hash = '';
        }
    }
}

function RaycastForLabel(event) {
    const canvas = renderer.domElement;
    const canvasRect = canvas.getBoundingClientRect();
    const mouse = new Vector2(
        ((event.clientX - canvasRect.left) / canvasRect.width) * 2 - 1,
        -((event.clientY - canvasRect.top) / canvasRect.height) * 2 + 1
    );

    const raycaster = new Raycaster();
    raycaster.setFromCamera(mouse, camera);
    return raycaster.intersectObjects(labelPoints);
}
