import { Tween, Easing, getAll } from '@tweenjs/tween.js'
import { camera, controls, center } from './main';

export function animateCameraMove(targetPosition) {
    const initialPosition = camera.position.clone();
    const initialTarget = controls.target.clone();
    const initialDistance = camera.position.distanceTo(controls.target);

    const defaultDistance = 1.5;
    const duration = 1500;

    const zoomOutDistance = initialDistance * 0.85 + defaultDistance * 0.15;
    const zoomInDistance = Math.min(initialDistance, defaultDistance);

    const direction = initialPosition.clone().sub(initialTarget).normalize();
    const zoomOutPosition = initialTarget.clone().add(direction.clone().multiplyScalar(zoomOutDistance));
    const zoomInPosition = targetPosition.clone().add(direction.clone().multiplyScalar(zoomInDistance));

    stopAllTweens();
    function createZoomTween(position, target, duration) {
        return new Tween(position)
            .to({ x: target.x, y: target.y, z: target.z }, duration)
            .easing(Easing.Quadratic.InOut)
            .onUpdate(() => {
                controls.update();
            });
    }

    if (initialDistance < defaultDistance) {
        const zoomOutTween = createZoomTween(camera.position, zoomOutPosition, duration / 2);
        zoomOutTween.onComplete(() => {
            const zoomInTween = createZoomTween(camera.position, zoomInPosition, duration / 2);
            zoomInTween.onComplete(() => {
                controls.enabled = true;
            });
            zoomInTween.start();
        });
        zoomOutTween.start();
    } else {
        const zoomTween = createZoomTween(camera.position, zoomInPosition, duration);
        zoomTween.start();
        zoomTween.onComplete(() => {
            controls.enabled = true;
        });
    }

    const panTween = createZoomTween(controls.target, targetPosition, duration);
    panTween.start();

    controls.enabled = false;
}

function stopAllTweens() {
    const allTweens = getAll();
    allTweens.forEach(tween => tween.stop());
}

export function resetCamera() {
    stopAllTweens();
    const duration = 1000;

    controls.enabled = false;
    const panTween = new Tween(controls.target)
        .to({
            x: center.x,
            y: center.y,
            z: center.z
        }, duration)
        .easing(Easing.Quadratic.InOut)
        .onUpdate(() => {
            controls.update();
        });

    var distance = (camera.position.clone().sub(center));
    var cameraZoomReset = distance.normalize().add(center).multiplyScalar(1.5);
    const zoomInTween = new Tween(camera.position)
        .to({
            x: cameraZoomReset.x,
            y: cameraZoomReset.y,
            z: cameraZoomReset.z
        }, duration)
        .easing(Easing.Quadratic.InOut)
        .onUpdate(() => {
            controls.update();
        })
        .onComplete(() => {
            controls.enabled = true;
        });

    panTween.start();
    zoomInTween.start();
}
