'use strict';

import Config from '../config.json' assert { type: 'json' };
import { ApplicationInsights } from '@microsoft/applicationinsights-web';

const ExploreMode = {
    none: 'None',
    embeddedViewerMode: 'EmbeddedViewerMode',
    webAppViewerMode: 'WebAppViewerMode'
}

const EventParameter = {
    title: 'Title',
    publisher: 'Publisher',
    version: 'Version',
    id: 'Id',
    imageTarget: 'ImageTargetName',
    enrichmentUid: 'EnrichmentUid',
    isPublished: 'IsPublished',
    isARAvailable: 'IsARAvailable',
    exploreMode: 'ExploreMode',
    quizQuestion: 'Question',
    quiz: 'Quiz',
    labelUid: 'LabelUid',
    source: 'Source',
};

const Event = {
    startLoadImageTargetsExperience: 'Start Load Image Targets',
    completeLoadImageTargetsExperience: 'Complete Load Image Targets',
    enteredScanningExperience: 'Entered Scanning Experience',
    openExperience: 'Open Experience',
    deleteExperience: 'Delete Experience',
    startDownloadExperience: 'Start Download Experience',
    finishedDownloadExperience: 'Finished Download Experience',
    openEnrichmentMenu: 'Open Enrichment Menu',
    videoWatchedToTheEnd: 'Video Watched To End',
    imageTargetQuizTracked: 'Image Target Quiz Tracked',
    imageTargetLinkTracked: 'Image Target Link Tracked',
    imageTargetAudioTracked: 'Image Target Audio Tracked',
    imageTargetTracked: 'Image Target Tracked',
    linkTriggered: 'Link Triggered',
    modelTriggered: 'Model Triggered',
    audioTriggered: 'Audio Triggered',
    videoTriggered: 'Video Triggered',
    modelLabelTriggered: 'Model Label triggered',
    modelLabelExpanded: 'Model Label Expanded triggered',
    quizQuestionAnswered: 'Quiz Question Answered',
    quizStartedTriggered: 'Quiz Started',
    quizCompletedTriggered: 'Quiz Completed',
};

export default class EventTracker {
    constructor() {
        this.appInsights = new ApplicationInsights({
            config: {
                connectionString: Config.connectionStrings.appInsights
            }
        });
        this.appInsights.loadAppInsights();
        this.appInsights.trackPageView()
    }

    openExperience() {
        this.trackEvent(Event.openExperience);
    }

    modelTriggered(enrichment) {
        const properties = {};
        properties[EventParameter.enrichmentUid] = enrichment.uid;
        this.trackEvent(Event.modelTriggered, enrichment.experience, properties);
    }

    modelLabelTriggered(label) {
        const properties = this.getLabelProperties(label);
        this.trackEvent(Event.modelLabelTriggered, properties);
    }

    modelLabelExpanded(label) {
        const properties = this.getLabelProperties(label);
        this.trackEvent(Event.modelLabelExpanded, properties);
    }

    trackEvent(name, experience, properties = {}) {
        const defaultProperties = this.getDefaultProperties(experience);
        properties = { ...properties, ...defaultProperties };
        this.appInsights.trackEvent({ name, properties });
    }

    getLabelProperties(label) {
        var properties = {};

        if (label?.uid !== undefined) {
            properties[EventParameter.labelUid] = label.uid;
        }

        if (label?.enrichment?.uid !== undefined) {
            properties[EventParameter.enrichmentUid] = label.enrichment.uid;
        }

        return properties;
    }

    getDefaultProperties(experience) {
        const properties = {};

        if (experience.title !== undefined) {
            properties[EventParameter.title] = experience.title;
        }

        if (experience.publisher !== undefined) {
            properties[EventParameter.publisher] = experience.publisher;
        }

        if (experience.version !== undefined) {
            properties[EventParameter.version] = experience.version;
        }

        if (experience.uid !== undefined) {
            properties[EventParameter.id] = experience.uid;
        }

        if (experience.isPublished !== undefined) {
            properties[EventParameter.isPublished] = experience.isPublished;
        }

        if (experience.isARAvailable !== undefined) {
            properties[EventParameter.isARAvailable] = experience.isARAvailable;
        }

        properties[EventParameter.source] = 'com.ludenso.explore.web';
        properties[EventParameter.exploreMode] = ExploreMode.embeddedViewerMode;

        return properties;
    }
}
