import { SphereGeometry, Mesh, PlaneGeometry, Texture, ShaderMaterial, Vector3, FileLoader } from 'three';
import LabelColliderMaterialVertexShader from '/shaders/LabelColliderMaterial.vert';
import LabelColliderMaterialFragmentShader from '/shaders/LabelColliderMaterial.frag';
import LabelMaterialVertexShader from '/shaders/LabelMaterial.vert';
import LabelMaterialFragmentShader from '/shaders/LabelMaterial.frag';
import LabelBorderMaterialVertexShader from '/shaders/LabelBorderMaterial.vert';
import LabelBorderMaterialFragmentShader from '/shaders/LabelBorderMaterial.frag';

export async function createLabel(point, labelNumber, title, description, uid, camera, scene, labelPoints) {
    const labelMaterial = await createLabelColliderMaterial(camera);
    const labelGeometry = new SphereGeometry(0.24, 32);
    const labelPoint = new Mesh(labelGeometry, labelMaterial);
    labelPoint.position.copy(point);
    labelPoint.userData = { number: labelNumber, title, description, uid };

    const texture = createLabelTexture(labelNumber);
    const textMesh = new Mesh(
        new PlaneGeometry(0.45, 0.45), await createTextMaterial(texture, camera)
    );
    const borderCircle = new Mesh(
        new PlaneGeometry(0.55, 0.55), (await createBorderMaterial(camera))
    );
    textMesh.position.copy(point);
    borderCircle.position.copy(point);
    textMesh.userData = { number: labelNumber };
    textMesh.position.set(0, 0, 0);
    textMesh.scale.set(0.1, 0.1, 0.1);
    borderCircle.scale.set(0.1, 0.1, 0.1);
    borderCircle.position.set(0, 0, 0);
    labelPoint.add(textMesh);
    labelPoint.add(borderCircle);
    scene.add(labelPoint);
    labelPoints.push(labelPoint);
    labelPoint.scale.set(0.1, 0.1, 0.1);
}

function createLabelTexture(number) {
    const canvas = document.createElement('canvas');
    const size = 64;
    canvas.width = size;
    canvas.height = size;
    const context = canvas.getContext('2d');

    context.fillStyle = 'black';
    context.font = 'bold 60px Arial';
    context.textAlign = 'center';
    context.textBaseline = 'middle';
    context.fillText(number, size / 2, size / 2);

    const texture = new Texture(canvas);
    texture.needsUpdate = true;
    return texture;
}

async function createLabelColliderMaterial(camera) {
    return new ShaderMaterial({
        vertexShader: LabelColliderMaterialVertexShader,
        fragmentShader: LabelColliderMaterialFragmentShader
    });
}

async function createTextMaterial(texture, camera) {
    const cameraRight = new Vector3(1, 0, 0).applyMatrix4(camera.matrixWorld);
    const cameraUp = new Vector3(0, 1, 0).applyMatrix4(camera.matrixWorld);
    const cameraForward = new Vector3(0, 0, 1).applyMatrix4(camera.matrixWorld);
    return new ShaderMaterial({
        vertexShader: LabelMaterialVertexShader,
        fragmentShader: LabelMaterialFragmentShader,
        uniforms: {
            time: { value: 0 },
            selected: { value: false },
            myTexture: { value: texture },
            uCameraPosition: { value: camera.position },
            uCameraDirection: { value: camera.position },
            uCameraRight: { value: cameraRight },
            uCameraUp: { value: cameraUp },
            uCameraForward: { value: cameraForward },
            screenHeight: { value: window.innerHeight}
        },
        transparent: true
    });
}

async function createBorderMaterial(camera) {
    const cameraRight = new Vector3(1, 0, 0).applyMatrix4(camera.matrixWorld);
    const cameraUp = new Vector3(0, 1, 0).applyMatrix4(camera.matrixWorld);
    const cameraForward = new Vector3(0, 0, 1).applyMatrix4(camera.matrixWorld);

    return new ShaderMaterial({
        vertexShader: LabelBorderMaterialVertexShader,
        fragmentShader: LabelBorderMaterialFragmentShader,
        uniforms: {
            time: { value: 0 },
            selected: { value: false },
            uCameraPosition: { value: camera.position },
            uCameraDirection: { value: camera.position },
            uCameraRight: { value: cameraRight },
            uCameraUp: { value: cameraUp },
            uCameraForward: { value: cameraForward },
            screenHeight: { value: window.innerHeight}
        },
        transparent: true
    });

}
